<template>
  <LayoutBox one="当前区域下的订单">
    <h4 class="title">订单列表</h4>
    <div class="tool">
      <div class="mt-2 mb-2">
        <span class="fs-lg mr-1 mt-1 ml-3">商品名称</span>
        <el-input v-model="prodName" placeholder="请输入内容" size="small" style="width: 240px"></el-input>
        <span class="fs-lg mr-1 mt-1 ml-3">筛选时间</span>
        <el-date-picker v-model="startTime" type="date" placeholder="选择日期" size="small"></el-date-picker> 至 <el-date-picker v-model="endTime" type="date" placeholder="选择日期" size="small"></el-date-picker>
      </div>
      <div class="btn_search">
        <el-button class="inquire1" type="success" size="small" @click="onSearch">查询</el-button>
        <el-button size="small" @click="onReset">重置</el-button>
      </div>
    </div>

    <el-table v-loading="tableLoading" :data="orderList" border max-height="400" style="width: 100%" size="mini" @expand-change="zydescription">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column type="expand">
        <template slot-scope="scope">
          <el-table border :data="scope.row.child" size="mini" :aaa="scope"><!-- aaa 没什么用 -->
            <el-table-column label="商品名" width="280" align="center" show-overflow-tooltip>
              <!-- eslint-disable-next-line vue/no-template-shadow -->
              <template slot-scope="itemScope">
                <div class="goods_box">
                  <el-image :src="itemScope.row.prodUrl" style="width: 50px; height: 50px" fit="cover"></el-image>
                  <span class="goods_name">{{ itemScope.row.prodName }}</span>
                </div>
              </template>
            </el-table-column>
            <el-table-column label="单价" align="center">
              <!-- eslint-disable-next-line vue/no-template-shadow -->
              <template slot-scope="itemScope">
                <span>￥{{ itemScope.row.price }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="count" label="数量" width="180" align="center" show-overflow-tooltip></el-table-column>
            <el-table-column label="状态" align="center">
              <!-- eslint-disable-next-line vue/no-template-shadow -->
              <template slot-scope="itemScope">
                <span v-if="itemScope.row.status === 0" style="color: #40ccae">待付款</span>
                <span v-else-if="itemScope.row.status === 1" style="color: #e39025">待发货</span>
                <span v-else-if="itemScope.row.status === 2" style="color: #49ca26">已发货</span>
                <span v-else-if="itemScope.row.status === 3" style="color: #c5fd68">待评价</span>
                <span v-else-if="itemScope.row.status === 4" style="color: #e02929">退款中</span>
                <span v-else-if="itemScope.row.status === 10" style="color: #666666">已完成</span>
                <span v-else-if="itemScope.row.status === 9" style="color: #999999">已关闭</span>
                <span v-else-if="scope.row.orderStatus === 0" style="color: #40ccae">待付款</span>
                <span v-else-if="scope.row.orderStatus === 1" style="color: #e39025">待发货</span>
                <span v-else-if="scope.row.orderStatus === 2" style="color: #49ca26">已发货</span>
                <span v-else-if="scope.row.orderStatus === 3" style="color: #c5fd68">待评价</span>
                <span v-else-if="scope.row.orderStatus === 4" style="color: #e02929">退款中</span>
                <span v-else-if="scope.row.orderStatus === 10" style="color: #666666">已完成</span>
                <span v-else-if="scope.row.orderStatus === 9" style="color: #999999">已关闭</span>
              </template>
            </el-table-column>
          </el-table>
        </template>
      </el-table-column>
      <el-table-column prop="orderCode" label="订单号" width="180" align="center" show-overflow-tooltip></el-table-column>
      <el-table-column prop="createTime" label="下单时间" align="center" width="170"></el-table-column>
      <el-table-column prop="userName" label="买家" align="center"></el-table-column>
      <el-table-column label="交易状态" align="center" width="130">
        <!-- 订单状态 0-待付款 1-待发货 2-待收货 3-已签收/待评价 4-退款中 5-待拼团 9-交易关闭 10-交易完成 小超状态0-代付款10-完成 -->
        <template slot-scope="scope">
          <div class="trading_status">
            <span v-if="scope.row.orderStatus === 0" style="color: #40ccae">待付款</span>
            <span v-else-if="scope.row.orderStatus === 1" style="color: #e39025">待发货</span>
            <span v-else-if="scope.row.orderStatus === 2" style="color: #49ca26">已发货</span>
            <span v-else-if="scope.row.orderStatus === 3" style="color: #c5fd68">待评价</span>
            <span v-else-if="scope.row.orderStatus === 4" style="color: #e02929">退款中</span>
            <span v-else-if="scope.row.orderStatus === 10" style="color: #666666">已完成</span>
            <span v-else-if="scope.row.orderStatus === 9" style="color: #999999">已关闭</span>
            <!-- <router-link :to="{ path: '/orderList/orderContent/', query: { id: scope.row.id }}" style="color: #459ae9">详情</router-link> -->
            <el-button v-if="scope.row.orderStatus === 0" size="mini" type="primary" @click="onRemind">提醒付款</el-button>
            <router-link v-else-if="scope.row.orderStatus === 1" :to="{ path: '/orderList/orderContent/ship', query: { id: scope.row.id }}"><el-button size="mini" type="primary">发货</el-button></router-link>
            <el-button v-else-if="scope.row.orderStatus === 2" size="mini" type="primary" @click="onOrderRecOvertime(scope)">延长收货</el-button>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="实收金额" align="center">
        <template slot-scope="scope">
          <span>￥{{ scope.row.payAmount }}</span>
        </template>
      </el-table-column>
      <el-table-column label="提成" align="center">
        <template slot-scope="scope">
          <span>￥{{ scope.row.pushMoney }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="orderRemarks" label="备注" align="center" show-overflow-tooltip></el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="onExtract(scope)">提取</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination background layout="prev, pager, next" :total="orderTotle" @current-change="handleCurrentChange"></el-pagination>
  </LayoutBox>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  components: {
    LayoutBox
  },

  data() {
    return {
      orderList: [], // 订单列表
      orderTotle: 0, // 订单总条数
      pageNo: 1, // 当期页数默认是1
      prodName: '', // 搜素是的商品名称
      startTime: '', // 选择下单开始时间
      endTime: '', // 选择下单结束时间
      goodsDataYJM: [], // 自定义字段商品信息
      tableLoading: false // 表格的loading
    }
  },

  mounted () {
    this._getOrderList()
  },

  methods: {
    // 分页
    handleCurrentChange (val) {
      this.pageNo = val
      this._getOrderList()
    },
    zydescription(data) {
      this._getOrderItem(data)
    },
    // 重置
    onReset () {
      this.prodName = ''
      this.startTime = ''
      this.endTime = ''
    },
    // 查询
    onSearch () {
      this._getOrderList()
    },
    // 点击提取
    onExtract (scope) {
      this.$message.warning('该功能暂未开放')
      console.log(scope)
    },

    // api ---------------------------------------
    // 根据userId查询订单(分页)
    _getOrderList () {
      let data = {
        pageNo: this.pageNo,
        prodName: this.prodName,
        orderStatus: this.orderStatus,
        startTime: this.startTime,
        endTime: this.endTime,
      }
      Object.assign(data)
      this.$http.post('/woho/areauser/findOrderByUserId', data)
        .then((res) => {
          if (res.data.success) {
            const orderList = res.data.data.list
            orderList.map((item) => Object.assign(item, { child: [] }))
            this.orderTotle = res.data.data.total
            this.orderList = orderList
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 获取订单明细列表
    _getOrderItem (data) {
      const DATA = {
        pageSize: 10000,
        orderHeaderId: data.id
      }
      this.$http.post('/woho/order/item/query', DATA)
        .then((res) => {
          res.data.data.list
          data.child = res.data.data.list
        })
    },
    // 订单收货延长时间
    apiOrderRecOvertime (id) {
      let data = {
        id: id
      }
      this.$http.post('/woho/order/head/orderRecOvertime', data)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('已延迟七天')
          } else {
            this.$message.error(res.data.msg)
          }
        })
    }
  }
}
</script>

<style lang="scss" scoped>
  .title{
    font-size: 19px;
    margin-top: 0px;
  }
  .tool{
    position: relative;
    .btn_search{
      position: absolute;
      bottom: 0px;
      right: 0px;
    }
  }
  .goods_box {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .goods_name{
    width: 200px;
    overflow: hidden;
  }
  .trading_status{
    display: flex;
    flex-direction: column;
  }
</style>
